
























import { Vue, Component } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class SidebarSewon extends Vue {
  onClickNavLink(name: string) {
    if (!this.isHere(name)) {
      this.$router.push({ name: name });
    }
  }
  isHere(name: string) {
    return this.$route.name === name;
  }
}
